import { Component, Input, OnInit, SimpleChange } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-permission',
  templateUrl: './permission.component.html',
  styleUrls: ['./permission.component.scss']
})
export class PermissionComponent implements OnInit {
  permission: any = {}
  freeAccount: any;
  @Input() id_employee: any
  @Input() old_permission_group: any
  visibleLogin: boolean = false; //biến cờ show/hide phân quyền đăng nhập
  visibleSale: boolean = false; //biến cờ show/hide phân quyền bán hàng
  visibleWarranty: boolean = false; //biến cờ show/hide phân quyền bảo hành, sửa chữa
  visibleDelivery: boolean = false; //biến cờ show/hide phân quyền giao hàng
  visibleCashflow: boolean = false; //biến cờ show/hide phân quyền dòng tiền
  visibleInvoice: boolean = false; //biến cờ show/hide phân quyền hóa đơn
  visibleReport: boolean = false; //biến cờ show/hide phân quyền báo cáo
  visibleConfig: boolean = false; //biến cờ show/hide phân quyền cấu hình
  visibleManage: boolean = false; //biến cờ show/hide phân quyền quản lý
  visibleNotifications: boolean = false; //biến cờ show/hide phân quyền thông báo
  visibleBranch: boolean = false //biến cờ show/hide phân quyền chi nhánh
  visibleAdmin: boolean = false //biến cờ show/hide phân quyền admin
  constructor(private vhAuth: VhAuth) { }

  ngOnInit() {
    this.freeAccount = this.vhAuth.getStore().freeaccount
    this.getData()
  }

  getData() {
    this.vhAuth.getPermission(this.id_employee)
      .then((permission) => {
        if (permission)
          this.permission = permission;



      })
  }
  ngOnChanges(change: SimpleChange) {
    if (change['old_permission_group']) this.getData()
  }
  updatePermission(value) {
    this.vhAuth.updatePermission(this.id_employee, value)
  }

}
